import Item1 from '../../images/item1.jpg'
import Item2 from '../../images/item2.jpg'
import Item3 from '../../images/item3.jpg'
import Item4 from '../../images/item4.jpg'
import Item5 from '../../images/item5.jpg'
import Item6 from '../../images/item6.jpg'
import { ADD_TO_CART,REMOVE_ITEM,SUB_QUANTITY,ADD_QUANTITY,ADD_SHIPPING } from '../actions/action-types/cart-actions'


const initState = {
    items: [
        {id:1,title:'NIKE  FLEECE JACKET', desc: "The Nike Sportswear Tech Fleece Men's Varsity Jacket features innovative fabric in a classic silhouette for warm, comfortable coverage.", price:120,img:"https://zihaowangusa.github.io/shopping_cart/static/media/item1.f8f4726f.jpg"},
        {id:2,title:'ADIDAS Z.N.E. HOODIE', desc: "The adidas Z.N.E. hoodie is created for the kind of comfort and is made of lightweight fabric with a vintage-soft feel.", price:100,img: "https://zihaowangusa.github.io/shopping_cart/static/media/item2.2aa35db0.jpg"},
        {id:3,title:'Under Armour HOODIE', desc: "The Under Armour Rival Fleece Boys Hoodie is made up of ultra-comfortable cotton-blend fleece so it is lightweight.",price:140,img: "https://zihaowangusa.github.io/shopping_cart/static/media/item3.fe242b81.jpg"},
        {id:4,title:'ADIDAS Terrex Two Boa', desc: "The ADIDAS Terrex Two Boa has a a Continental rubber outsole and the breathable mesh upper will keep your feet cool.", price:160,img:"https://zihaowangusa.github.io/shopping_cart/static/media/item4.667e69f9.jpg"},
        {id:5,title:'ADIDAS Solar Boost', desc: "The Adidas solar boost is a good platform for neutral runners and the midsole locks the foot in place with a lightweight fabric.", price:200,img: "https://zihaowangusa.github.io/shopping_cart/static/media/item5.56e98852.jpg"},
        {id:6,title:'ADIDAS Terrex Agravic', desc: "The ADIDAS Terrex Agravic has full-length Continental rubber outsole to keep traction even in wet and muddy conditions.",price:180,img: "https://zihaowangusa.github.io/shopping_cart/static/media/item6.1571d9e0.jpg"}
    ],
    addedItems:[],
    total: 0

}
const cartReducer= (state = initState,action)=>{
   
    //INSIDE HOME COMPONENT
    if(action.type === ADD_TO_CART){
          let addedItem = state.items.find(item=> item.id === action.id)
          //check if the action id exists in the addedItems
         let existed_item= state.addedItems.find(item=> action.id === item.id)
         if(existed_item)
         {
            addedItem.quantity += 1 
             return{
                ...state,
                 total: state.total + addedItem.price 
                  }
        }
         else{
            addedItem.quantity = 1;
            //calculating the total
            let newTotal = state.total + addedItem.price 
            
            return{
                ...state,
                addedItems: [...state.addedItems, addedItem],
                total : newTotal
            }
            
        }
    }
    if(action.type === REMOVE_ITEM){
        let itemToRemove= state.addedItems.find(item=> action.id === item.id)
        let new_items = state.addedItems.filter(item=> action.id !== item.id)
        
        //calculating the total
        let newTotal = state.total - (itemToRemove.price * itemToRemove.quantity )
        console.log(itemToRemove)
        return{
            ...state,
            addedItems: new_items,
            total: newTotal
        }
    }
    //INSIDE CART COMPONENT
    if(action.type=== ADD_QUANTITY){
        let addedItem = state.items.find(item=> item.id === action.id)
          addedItem.quantity += 1 
          let newTotal = state.total + addedItem.price
          return{
              ...state,
              total: newTotal
          }
    }
    if(action.type=== SUB_QUANTITY){  
        let addedItem = state.items.find(item=> item.id === action.id) 
        //if the qt == 0 then it should be removed
        if(addedItem.quantity === 1){
            let new_items = state.addedItems.filter(item=>item.id !== action.id)
            let newTotal = state.total - addedItem.price
            return{
                ...state,
                addedItems: new_items,
                total: newTotal
            }
        }
        else {
            addedItem.quantity -= 1
            let newTotal = state.total - addedItem.price
            return{
                ...state,
                total: newTotal
            }
        }
        
    }

    if(action.type=== ADD_SHIPPING){
          return{
              ...state,
              total: state.total + 6
          }
    }

    if(action.type=== 'SUB_SHIPPING'){
        return{
            ...state,
            total: state.total - 6
        }
  }
    
  else{
    return state
    }
    
}

export default cartReducer
